import React, { useRef, useState,useEffect } from 'react';
import * as d3 from 'd3';
import ForceLabel from './force_label';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import IconButton from '@mui/material/IconButton';

const colors = [
    "#9edae5", "#17becf", "#dbdb8d", "#bcbd22", "#c7c7c7", "#7f7f7f", 
    "#f7b6d2", "#e377c2", "#c49c94", "#8c564b", "#c5b0d5", "#9467bd", 
    "#ff9896", "#d62728", "#98df8a", "#2ca02c", "#ffbb78", "#ff7f0e", 
    "#aec7e8", "#1f77b4"
]

const colors2 = [
    "#5778a4", // blue
    "#e49444", // orange
    "#d1615d", // red
    "#85b6b2", // teal
    "#6a9f58", // green
    "#e7ca60", // yellow
    "#a87c9f", // purple
    "#f1a2a9", // pink
    "#967662", // brown
    "#b8b0ac"  // grey
  ];
  
var legend_dict = {
    5:{male: "#5778a4", female: "#e49444", upper: "#d1615d", middle:"#85b6b2", lower: "#6a9f58",
    asian:"#e7ca60", african:"#a87c9f", white:"#f1a2a9", hispanic:"#967662", native_americans:"#b8b0ac", 
    arabs:"#1f77b4", jews:"#aec7e8"},
    4:{gender: "#2ca02c", socioeco:"#d62728", ethnics: "#9467bd"}
}

var cntr_dict = {male: 0, female: 1, upper: 2, middle:3, lower: 4,
    asian:5, african:6, white:7, hispanic:8, native_americans:9, 
    arabs:10, jews:11}




function Scatterplot(props) {
  const { width, height, data , labels, colorcol, showContour, showScatter, showLabel} = props;
  const margin = {top:80, left:100, right:80, bottom:120}
  const ref = useRef();
  const legendref = useRef();
  const [zoomval, setZoomVal] = useState(1);
  const [pan, setPan] = useState([0,0]);
  const [nodes, setNodes] = useState(null);






  const color_dict = legend_dict[+colorcol]

  console.log(color_dict)



  useEffect(() => {

      // Setup the zoom behavior
    const zoom = d3.zoom()
    .scaleExtent([1, 1]) // Limit the zoom scale
    .on('zoom', (event) => {
        
        // Apply the zoom transform to the <g> element
        g.attr('transform', 'translate(+'+event.transform.x+','+event.transform.y+')');
        //console.log(event.transform.k)
        setPan([event.transform.x,event.transform.y])
        d3.selectAll(".tooltip").style("opacity", 0)

    })

    


    const svg = d3.select(ref.current)
    .call(zoom);
    


    svg.selectAll('*').remove()
    svg.attr('transform', 'translate(+'+pan[0]+','+pan[1]+')scale('+zoomval+')')


    

    var g = svg.append("g");
    
    // Set scales for the scatterplot
    const xScale = d3.scaleLinear()
      .domain([d3.min(data, d => d[0]), d3.max(data, d => d[0])])
      .range([margin.left, width-margin.right]);

    const yScale = d3.scaleLinear()
      .domain([d3.min(data, d => d[1]), d3.max(data, d => d[1])])
      .range([height-margin.bottom, margin.top]);


    // Tooltips
    const tooltip = d3.select("body").append("div")
        .attr("class", "tooltip")
        .style("opacity", 0)
        .style("margin-right", "350px")
        .style("position", "absolute");

    d3.select("#chatcontent").selectAll('*').remove()

    // Chat content
    const chatcontent = d3.select("#chatcontent").append("div")

    if (showScatter){
    // Bind data to circles and add tooltips
    g.selectAll('circle')
        .data(data)
        .enter()
        .append('circle')
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => yScale(d[1]))
        .attr('r', 4/zoomval)
        .style("opacity", d =>  d[3]===-1? 0.1 : (showLabel?0.2:0.7))
        .attr('fill', d => color_dict[d[+colorcol]])
        .attr('stroke', 'black')  // Add this line for the boundary color
        .attr('stroke-width', 0.5/zoomval)  // Add this line for the boundary width
        .on("click", (event, d) => {

            var chat = d[2].replace('###', "").replace(/###/g, "<br/><br/>");

            chatcontent.transition()
            .duration(100)
            .style("opacity", 1);
            chatcontent.html(chat)

            g.selectAll('circle')
            .transition().duration(100)
            .style("opacity", d =>  d[3]===-1? 0.1 : (showLabel?0.2:0.7)) 
            .attr('stroke-width', 0.5/zoomval) 
            .attr('r', 4/zoomval);
            // Add the hovered to the current circle
            d3.select(event.currentTarget).transition().duration(100)
                .style("opacity", 1) 
                .attr('stroke-width', 1/zoomval)  // Add this line for the boundary width
                .attr('r', 10/zoomval);

        })
        .on("mouseover", (event, d) => {
            
            tooltip.transition()
                .duration(100)
                .style("opacity", .9);
            tooltip.html("<b>Keywords:</b> "+ d[3].slice(0,10)+"<br/><br/><b>Parent Label:</b> "+d[4]+" <br/><b>Child Label:</b> "+d[5])
                .style("font-size", "15px")
                .style("left", (event.pageX + 5) + "px")
                .style("top", (event.pageY - 28) + "px");
        })
        .on("mouseout", (d) => {            
            tooltip.transition()
                .duration(500)
                .style("opacity", 0);
        });
    }

    

    // Render the legend
    var keys = Object.keys(color_dict)
   
    const legend = d3.select(legendref.current).style('height', keys.length*30);
    legend.selectAll('*').remove()


    legend.selectAll('circle')
        .data(keys)
        .enter()
        .append('circle')
        .attr('cx', (d,i) => 10)
        .attr('cy', (d,i) => 18+ 30*i)
        .attr('r', 7)
        .attr('fill', d => color_dict[d])
        .attr('stroke', 'black')  // Add this line for the boundary color
        .attr('stroke-width', 0.5)  // Add this line for the boundary width

    console.log(keys)

    legend.selectAll("mylabels").data(keys)
        .enter()
        .append("text")
            .attr("x", 20)
            .attr("y", function(d,i){ return 20 + i*30}) // 100 is where the first dot appears. 25 is the distance between dots
            .style("fill",'black')
            .text(function(d){ return d})
            .attr("text-anchor", "left")
            .style("alignment-baseline", "middle")

    
    // Bind data to text elements and add labels
    if ((labels != null)&(showLabel)){
    var show_lbl = new Array(labels.length).fill(true);

    var lbls = g.selectAll('text.label')
        .data(labels)
        .enter()
        .filter(function(d0, i){
            var isCollide = false,
                x0 = xScale(d0[0]),
                y0 = yScale(d0[1]);
            labels.forEach(function(d1,j){
                
              if (show_lbl[i]&show_lbl[j]){
              // if it has a collision with another, stop looking
              //if (!isCollide){
                // if it's not itself
                if (d0[2] != d1[2]){
                  var x1 = xScale(d1[0]),
                      y1 = yScale(d1[1]);
                   // if they overlap     
                   //isCollide = Math.pow((x1-x0),2) <=2/zoomval  || Math.pow((y1-y0),2) <= 30/zoomval       
                   isCollide = Math.pow((x1-x0),2) + Math.pow((y1-y0),2) <= 1000/zoomval//Math.pow((d0.r+d1.r), 2);
                //}
                show_lbl[i]= !isCollide
              }
            }
            });
            return show_lbl[i]//!isCollide;
          })            
        .append('text')
        .attr('class', 'label')
        .attr('fill', 'black')  
        .attr('x', d => xScale(d[0])) 
        .attr('y', d => yScale(d[1]))
        .attr('dy', '.35em')  
        .attr('text-anchor', 'middle')
        .text(d => `${d[2]}`)
        .attr('stroke', 'white')  
        .attr('stroke-width', 0.3/zoomval+'px') 
        .attr('font-size', 16/zoomval+'px')
        .attr('font-weight', '800').call(getBB);   

    lbls.insert("rect","text")
        .attr("width", function(d){return d.bbox.width})
        .attr("height", function(d){return d.bbox.height})
        .style("fill", "white");
    
    function getBB(selection) {
        selection.each(function(d){d.bbox = this.getBBox();})
        

    }

    var label_nodes = labels.map(d => ({
            fx: xScale(d[0]),
            fy: yScale(d[1]),
            id: d[2],
            // Add other node properties here if necessary
          }));

    setNodes(label_nodes)

    }
    

    //make contours


    // Compute the density contours.
    const contours2 = d3.contourDensity()
    .x(d => xScale(d[0]))
    .y(d => yScale(d[1]))
    .size([width, height])
    .bandwidth(30)
    .thresholds(30)
    (data);

    if (showContour){
    // Append the contours.
    g.append("g")
        .attr("fill", "none")
        .attr("stroke", "steelblue")
        .attr("stroke-linejoin", "round")
        .selectAll()
        .data(contours2)
        .join("path")
        .style("opacity", 0.5) 
        .attr("stroke-width", (d, i) => i % 5 ? 0.25 : 1)
        //.attr("stroke", 'red')
        .attr("d", d3.geoPath());
    }



  }, [data, labels, colorcol, width, height, zoomval, showContour, showScatter, showLabel]);

  return (
  <>
<div style={{ position: 'absolute', top: "-25px", left: '250px', width: '60%'}}>

</div>

  <div style={{ position: 'absolute', top: "10px", left: '16%', width: '60%', height:'93%',
                 boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.5)',  // Drop shadow
                borderRadius: '20px' ,                         // Curved edges
                fontFamily: 'Perpetua',  // Setting the font family
                padding: '15px',
                overflow:'scroll',
                textAlign:'left',                
            }}>
    <svg  ref={ref} width={width} height={height} ></svg>


  </div>
  <div style={{ position: 'absolute', top: "50%", left: '20px', width: '11%', backgroundColor: 'rgba(0, 0, 0, 0.1)',
                 boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.5)',  // Drop shadow
                borderRadius: '20px' ,                         // Curved edges
                fontFamily: 'Perpetua',  // Setting the font family
                padding: '15px',
                overflow:'scroll',
                textAlign:'left',                
            }}>
            <b>Legend</b>
            

            <svg ref={legendref} height={'100px'}></svg>
        </div>

<div style={{ position: 'absolute', top: "91%", left: '17%', backgroundColor: 'rgba(0, 0, 0, 0.1)',
                 boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.5)',  // Drop shadow
                borderRadius: '20px' ,}}>
    <IconButton aria-label="delete">
        <AddIcon onClick={()=>{console.log(zoomval);setZoomVal(zoomval*1.5)}}/>
      </IconButton>
      <IconButton aria-label="delete">
        <RemoveIcon onClick={()=>{console.log(zoomval);setZoomVal(zoomval/1.5)}}/>
      </IconButton>

</div>

    </>

  )
}

export default Scatterplot;
