import React, { useEffect, useRef, useMemo } from 'react';
import * as d3 from 'd3';



const width = 0.6*window.innerWidth
const height = 0.85*window.innerHeight

const ForceGraph = ({ nodes }) => {


  const nodesMemo = useMemo(() => nodes, [nodes]);

  const svgRef = useRef();

  
  useEffect(() => {
    const svg = d3.select(svgRef.current)
      .attr('viewBox', [0, 0, width+100, height+100])

    const simulation = d3.forceSimulation(nodes)
      .force('charge', d3.forceManyBody().strength(-3))
      .force('center', d3.forceCenter(width / 2, height / 2))
      //.force('x', d3.forceX(d => d.fx || width / 2).strength(-0.01)) // Add forceX
      //.force('y', d3.forceY(d => d.fy || height / 2).strength(-0.01)); // Add forceY
  
      if (nodes){

    // Create text nodes
    const node = svg.append('g')
      .selectAll('text')
      .data(nodes)
      .join('text')
      .text(d => d.id)
      .attr('cursor', 'pointer')
      .attr('fill', colorByGroup)
      .call(drag(simulation));

    simulation.on('tick', () => {
  node
    .attr('x', d => {
      let fixedX = d.fx; // Preserve the original fixed position
      d.fx = null;       // Release the fixed position
      return fixedX || d.x;
    })
    .attr('y', d => {
      let fixedY = d.fy; // Preserve the original fixed position
      d.fy = null;       // Release the fixed position
      return fixedY || d.y;
    });
  })

}

    function drag(simulation) {
      function dragstarted(event) {
        if (!event.active) simulation.alphaTarget(0.3).restart();
        event.subject.fx = event.subject.x;
        event.subject.fy = event.subject.y;
      }
      
      function dragged(event) {
        event.subject.fx = event.x;
        event.subject.fy = event.y;
      }
      
      function dragended(event) {
        if (!event.active) simulation.alphaTarget(0);
        event.subject.fx = null;
        event.subject.fy = null;
      }
      
      return d3.drag()
        .on('start', dragstarted)
        .on('drag', dragged)
        .on('end', dragended);
    }

    function colorByGroup(d) {
      // Define color by group logic
      // For simplicity, return a fixed color
      return '#333'; // Darker color for better visibility on text
    }

    // Clean up simulation on dismount
    return () => simulation.stop();
  }, [nodes]);
  
  return <svg ref={svgRef}></svg>;
};

export default ForceGraph;
